<template>
	<div id="bottomMenu" @click.prevent="changePage">
		<mt-tabbar fixed v-model="selected">
		  <mt-tab-item  id="/index/home">
		    <!-- 运输 icon by Icons8 -->
		    <img slot="icon" class="icon icons8-运输" src="data:image/png;base64,">
		    <div class="fontSize">外卖</div>
		  </mt-tab-item>
		  <mt-tab-item id="/index/search" >
		    <!-- 罗盘 icon by Icons8 -->
		    <img slot="icon" class="icon icons8-罗盘" src="data:image/png;base64,">
		    <div class="fontSize">搜索</div>
		  </mt-tab-item>
		  <mt-tab-item id="/index/order" >
		    <!-- 店 icon by Icons8 -->
		    <!-- Bill icon by Icons8 -->
		    <img slot="icon" class="icon icons8-Bill" src="data:image/png;base64,">
		    <div class="fontSize">订单</div>
		  </mt-tab-item>
		  <mt-tab-item id="/index/person" >
		    <!-- 性别中性用户 icon by Icons8 -->
		    <img slot="icon" class="icon icons8-性别中性用户" src="data:image/png;base64,">
		    <div class="fontSize">我的</div>
		  </mt-tab-item>
		</mt-tabbar>
	</div>
</template>
<script>
export default{
	name: 'bottomMenu',
	data(){
		return {
			selected: this.$route.path
		}
	},
	methods:{
		changePage:function(){
			this.$router.push(this.selected);
			console.log("selected: ",this.selected);
			console.log("$route.path: ",this.$route.path);
		}
	},
	watch:{
		'$route.path':function(){
			this.selected = this.$route.path;	
			console.log("selected: ",this.selected);
			console.log("$route.path: ",this.$route.path);
		}
	}
}
</script>
<style >
#bottomMenu{
	overflow: hidden;
}
#bottomMenu .mint-tab-item-icon{
    width: 0.42666rem !important;
    height: 0.42666rem !important;
}
#bottomMenu .fontSize{
	font-size: 0.2666666rem !important;
}
</style>


